
import XHeader from 'vux/src/components/x-header/index.vue'

import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import scroll from '@/components/scroll/scroll'
export default{
components: {
XHeader,InlineLoading,Toast,scroll
},
data() {
return {
noData: false,
pageIndex: 1,
pageSize: 15,
list: [],
}
},
mounted() {
this.getHospitalRecordList(true);
},
methods: {

getHospitalRecordList(upOrDown) {
if (upOrDown) {
this.pageIndex = 1;
}
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "inHospitalLog",
"MSH.2": "list",
"CHZBM": this.$root.loginInfo.CHZBM,
"pageIndex": this.pageIndex,
"pageSize": this.pageSize,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(typeof res != 'undefined' && typeof res.rows != 'undefined') {
if (res.pageNumber == 1) {
this.pageIndex = 1,
this.list = [];
}
if (res.rows.length > 0) {
this.noData = false;
this.pageIndex++;
this.list = [...this.list,...res.rows];
} else {
if (res.pageNumber > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.noData = true;
}
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
})
},

gotoDetail(JGMC, JGBM, LSH, ZYH, N) {
this.$router.push({name: "HospitalRecordsDetail", params: {JGMC: JGMC, JGBM: JGBM, LSH: LSH, ZYH: ZYH, N: N}});
}
}
}



